.formInput {
    display: flex;
    flex-direction: column;
    width: 280px;
    /* margin: -4px 0; */
}

input {
    padding: 15px;
    margin: 10px 0;
    border-radius: 5px;
    border: 1px solid gray;
}

/* label {
    font-size: 14px;
    color: gray;
} */

span {
    font-size: 12px;
    padding: 3px;
    color: red;
    display: none;
}

input:invalid[focused="true"] {
    border: 1px solid red;
}

input:invalid[focused="true"]~span {
    display: block;
}